import gsap, { Linear } from "gsap";
import { Container, Sprite, Text, Texture } from "pixi.js";

export class ScorePlace extends Container {
    private scoreTxt: Text;
    private scoreNumber: number;
    constructor() {
        super();
        const back = Sprite.from(Texture.WHITE);
        back.width = 300;
        back.height = 70;
        back.tint = 0x3a3a3a;
        this.addChild(back);
        this.scoreNumber = 0;
        this.scoreTxt = new Text(this.scoreFormatted(0), {fontSize: 50, fill: 0xFFFFFF, align: "center", letterSpacing: 10});
        this.scoreTxt.anchor.set(0.5, 0.5);
        this.scoreTxt.position.set(back.width*0.5, back.height*0.5);
        this.addChild(this.scoreTxt);
    }
    public set score(s: number) {
        const p = {score: this.scoreNumber};
        this.scoreTxt.text = this.scoreFormatted(this.scoreNumber);
        gsap.to(p, {score: s, duration: 0.1, ease: Linear.easeNone, onUpdate: ()=> {
            this.scoreTxt.text = this.scoreFormatted(Math.floor(p.score));
        }});
        this.scoreNumber = s;
    }
    private scoreFormatted(s: number) {
        const digits = 6;
        let t = s.toString();
        const rest = digits - t.length;
        for (let i = 0; i < rest; i++) {
            t = "0" + t;
        }
        return t;
    }
}