import gsap from "gsap";
import { Container, IPointData, NineSlicePlane, Sprite, Text, Texture } from "pixi.js";
import { gameSettings } from "../../../core/BaseGame";

export type TCondition = ()=>{success: boolean, data?: any};
export enum ETutorial {
    Heart = "heart",
    NewLine = "newLine",
    Recharge = "recharge",
    Unbreak = "unbreak",
}
export class Tutorial extends Container {
    private allConditions: Map<ETutorial, TCondition>;
    private textMap: Map<ETutorial, string>;
    private tutorial: NineSlicePlane;
    private txt: Text;
    private back: Sprite;
    constructor() {
        super();
        this.allConditions = new Map<ETutorial, TCondition>();
        this.textMap = new Map<ETutorial, string>();
        this.textMap.set(ETutorial.Heart, "The HEART is free and happy now!");
        this.textMap.set(ETutorial.NewLine, "It's time for a new Emoji!");
        this.textMap.set(ETutorial.Recharge, "The battery is full! Take a Bonus!");
        this.textMap.set(ETutorial.Unbreak, "This icon you cannot destroy.");

        this.back = Sprite.from(Texture.WHITE);
        this.back.interactive = true;
        this.back.width = gameSettings.originalWidth;
        this.back.height = gameSettings.originalHeight;
        this.addChild(this.back);
        this.back.position.set(-this.back.width*0.5, -this.back.height*0.5);
        this.back.tint = 0x000000;
        this.back.alpha = 0.3;

        this.tutorial = new NineSlicePlane(Texture.from("tutorial.png"), 52, 52, 90, 93);
        this.addChild(this.tutorial);

        this.txt = new Text("", {fontSize: 50, fontWeight: "bold", fill: 0xffffff, wordWrap: true, wordWrapWidth: 300});
        this.addChild(this.txt);

        this.visible = false;
    }
    public addCondition(t: ETutorial, c: TCondition) {
        this.allConditions.set(t, c);
    }
    public update() {
        for (const [e,c] of this.allConditions) {
            const result = c();
            if (result.success) {
                this.show(e, result.data);
                this.allConditions.delete(e);
                break;
            }
        }
    }

    public show (type: ETutorial, data?: any) {
        const text = this.textMap.get(type);
        this.txt.text = text;
        this.tutorial.scale.set(1,1);
        this.tutorial.pivot.set(this.txt.width + 100, this.txt.height + 100);
        this.tutorial.width = this.txt.width + 100;
        this.tutorial.height = this.txt.height + 100;
        switch(type) {
            case ETutorial.Heart:
            case ETutorial.NewLine:
            case ETutorial.Recharge:
            case ETutorial.Unbreak:
                const pos = data.pos as IPointData;
                const scale = data.scale as IPointData;
                this.tutorial.scale.x *= scale.x;
                this.tutorial.scale.y *= scale.y;
                this.tutorial.position.copyFrom(pos);
                this.txt.position.copyFrom(pos);
                //console.log(scale);
                if (scale.x === 1) {
                    this.txt.x -= 60 + this.txt.width;
                } else {
                    this.txt.x += 52;
                }
                if (scale.y === 1) {
                    this.txt.y -= this.txt.height;
                } else {
                    this.txt.y += 52;
                }
                break;
        }
        this.visible = true;
        gameSettings.paused = true;
        gsap.globalTimeline.pause();
        window.setTimeout(()=> {
            this.back.on("pointerdown", ()=> {
                this.hide();
            });
        }, 100)
    }
    public hide() {
        this.visible = false;
        gameSettings.paused = false;
        gsap.globalTimeline.resume();
        this.back.off("pointerdown")
    }
}