import { Graphics } from "pixi.js";
import { TextedButton } from "../../../core/ui/TextedButton";

export class BackButton extends TextedButton {
    constructor() {
        super("Menu", 40, 0xffffff, 0x000000);
        this.txt.style.fontWeight = "bold";
        this.btn.alpha = 0.000001;
        this.btn.width = 180;
        this.btn.height = 50;

        const arrow = new Graphics();
        arrow.beginFill(0,1);
        arrow.lineTo(25, -15);
        arrow.lineTo(25, 15);
        arrow.lineTo(0, 0);
        this.addChild(arrow);
        arrow.position.set(-95, 0);
    }
}